.page_container3 {
  display: flex;
  flex: 5;
  margin-left: 10px;
  width: 80vw;
  margin-top: 10px;
  justify-content: center;
}

.container3 {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: flex-start;
  margin: 20px;
  background-color: #f9f9f9;
  border-radius: 10px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  transition: box-shadow 0.3s ease;
}

.container3:hover {
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.3);
}
.form_column {
  flex-basis: 45%;
  padding: 0 10px;
}

.form_container3 {
  width: 600px;
  padding: 20px;
  box-shadow: 5px 5px 10px rgb(53, 14, 228);
    background-color: #f3f3f3;
  border-radius: 10px;
  animation: fadein 1s;
}

.form_container3 h2 {
  margin: 0;
  padding-bottom: 10px;
  border-bottom: 2px solid #007bff;
  font-size: 24px;
  text-align: center;
}

.form_group3 {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 20px;
}

label {
  font-weight: bold;
  display: block;
  margin-top: 10px;
  color: #333;
}

input {
  width: 100%;
  padding: 12px;
  margin-bottom: 20px;
  border-radius: 5px;
  border: 1px solid #ccc;
  transition: border-color 0.3s ease;
}

input:focus {
  border: 1px solid #007bff;
}

.right_column1 {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

button[type='submit'] {
  background-color: #007bff;
  color: #fff;
  border: none;
  padding: 12px 20px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s ease;
}

button[type='submit']:hover {
  background-color: #0056b3;
}
