
:root {
  --primary-color: #ff6b6b; /* Red */
  --secondary-color: #f9c74f; /* Yellow */
  --accent-color: #90be6d; /* Green */
}
.page-container4 {
  display: flex;
  flex: 5;
  margin-left: 10px;
  width: 80vw;
  margin-top: 10px;
  justify-content: center;
}
.container4 {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: flex-start;
  margin: 20px;
  background-color: #f9f9f9;
  border-radius: 10px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  transition: box-shadow 0.3s ease;
}

.container4:hover {
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.3);
}

.form-container4 {
  width: 600px;
  padding: 20px;
  box-shadow: 5px 5px 10px rgb(53, 14, 228);  background-color: #f3f3f3;
  border-radius: 10px;
  background-color: #f3f3f39a;
  animation: fadein 1s;
}

.form-group4 {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 20px;
}

.form-container4 h2 {
  margin: 0;
  padding-bottom: 10px;
  border-bottom: 2px solid #007bff;
  font-size: 24px;
  color: #007bff;
}

.form-column {
  flex-basis: 45%;
}

label {
  font-weight: bold;
  display: block;
  margin-top: 10px;
  color: #333;
}

input {
  width: 100%;
  padding: 12px;
  margin-bottom: 20px;
  border-radius: 5px;
  border: 1px solid #ccc;
  transition: border-color 0.3s ease, box-shadow 0.3s ease;
}

input:focus {
  border: 1px solid #007bff;
}

button {
  background-color: #007bff;
  color: #fff;
  border: none;
  padding: 12px 20px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #0056b3;
}

h2 {
  text-align: center;
}

/* Responsive layout */
@media (max-width: 768px) {
  .container4 {
    flex-direction: column;
    align-items: center;
  }
}

/* Fadein Animation */
@keyframes fadein {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.form-container4:hover {
  box-shadow: 5px 5px 15px rgba(53, 14, 228, 0.7);
}
@media (max-width: 768px) {
  .form-group4 {
    flex-direction: column;
    align-items: center;
  }
}